@mixin flex-center($df: flex, $jc: center, $ai: center) {
  display: $df;
  justify-content: $jc;
  align-items: $ai;
}

@mixin mq($type) {
  $mobile: 320px;
  $tablet: 768px;
  $desktop: 1280px;

  @if $type == 'mob' {
    @media screen and (min-width: $mobile) {
      @content;
    }
  }

  @if $type == 'mob-only' {
    @media screen and (max-width: ($tablet - 0.02)) {
      @content;
    }
  }

  @if $type == 'tab' {
    @media screen and (min-width: $tablet) {
      @content;
    }
  }

  @if $type == 'tab-only' {
    @media screen and (min-width: $tablet) and (max-width: ($desktop - 0.02)) {
      @content;
    }
  }

  @if $type == 'desk' {
    @media screen and (min-width: $desktop) {
      @content;
    }
  }

  @if $type == 'not-desk' {
    @media screen and (max-width: ($desktop - 0.02)) {
      @content;
    }
  }
}
